#root {
    background: #eee;
    height: 100vh;
}

.country-wrapper {
    height: 400px;
    width: 300px;
    border-radius: 10px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.10), 0 10px 10px rgba(0,0,0,0.13);
    margin: 20px auto;
}

header {
    position: relative;
    width: 100%;
    height: 30%;
    background: #003e9f;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.country-photo {
    position: absolute;
    width: 115px;
    height: 115px;
    border-radius: 50%;
    border: 10px solid #003e9f;
    left: 0;
    right: 0;
    bottom: -50px;
    margin: 0 auto;
}

.country-info {
    padding: 55px 5px 65px;
    font-family: 'Roboto Condesed';
}

h1 {
    font-size: 28px;
    letter-spacing: 1.2px;
    text-align: center;
    font-weight: 600;
    color: #323232
}

h2 {
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 1.2px;
    color: #5f5f5f;
}

.info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.info div {
    text-align: center;
    width: 33%;
}

.info div span {
    display: block;
}

.info div span:nth-of-type(1) {
    font-weight: bold;
    font-size: 24px;
    color: #323232;
}

.info div span:nth-of-type(2) {
    font-size: 12px;
    text-transform: uppercase;
    color: #5f5f5f;
    letter-spacing: .5px;
    padding: 6px 0;
}

.countries-list {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.single-country {
    width: 26%;
    height: 300px;
    margin-top: 50px;
    position: relative;
}

.country-logo-wrapper {
    height: 100%;
    width: 100%;
}

.country-logo {
    width: 400px;
    height: 250px; 
}

input {
    color: #bdc3c7;
    text-align: center;
}

button {
    position: absolute;
    bottom: -20px;
    left: 165px;
    -webkit-transition: 0.6s;
    transition: 0.6s;
    border-radius: 5px;
    border: 1px solid gray;

}

button:hover {
    opacity: 0.7;
    background-color: lightcoral;
}

.select-button {
    position: relative;
    left: 45%;
    background-color: #DDDDDD;
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    text-transform: none;
    letter-spacing: .5px;
    padding: 5px;
    height: 50px;
    margin-left: 10px;
    border-radius: 5px;
    text-transform: uppercase;
}

